.videogallery {
  display: flex;
  flex-direction: column;
  gap: 40px;
  justify-content: space-between;
}
.videogallery h1 {
  font-size: 44px;
  font-weight: bold;
}
.videogallery__about-center {
  display: flex;
  justify-content: space-between;
  align-items: center;
}


.videogallery__text {
  max-width: 500px;
  align-self: center;
  font-size: 24px;
}
.videogallery__purposes {
  display: flex;
  justify-content: space-between;
  gap: 80px;
}
.purposes__text {
  max-width: 500px;
  font-size: 24px;
}
.videogallery__video-participants {
  display: flex;
  justify-content: space-between;
}
.video-participants__text {
  max-width: 500px;
  font-size: 24px;
}

.videogallery__video-list {
justify-content: space-between;
  margin-top: 80px;
  display: flex;
  /* flex-direction: column; */
  flex-wrap: wrap;
  gap: 40px;
}
.video-list__video-item {
  /* overflow: hidden; */
  max-width: 500px;
  font-size: 24px;
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
  /* justify-content: space-between; */
  gap: 20px;
}

.video-item__video iframe {
 max-width: 500px;
 max-height: 300px;
}

.video-item__description {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
/* .video-list__video-item:nth-child(2n) {
  flex-direction: row-reverse;
} */
hr {
  border: none;
  height: 2px;
  background-color: rgb(129, 129, 129);
}
@media  (max-width: 411px) {
  .videogallery iframe {
    max-width: 640px;
    max-height: 400px;
}
}
@media  (max-width: 915px) {
  .videogallery__about-center {
    flex-wrap: wrap;
    gap: 40px;
  }
  .videogallery__video-list {
justify-content: center;
    }  
  .videogallery iframe {
    max-width: 400px;
    height: 250px;
  }
  .videogallery__video-participants {
    flex-direction: column-reverse;
    gap: 40px ;
  }
  .videogallery__purposes {
    flex-wrap: wrap;
  }
}
