.header {
  padding: 0 40px;
  display: flex;
  justify-content: flex-end;
  height: 160px;
  gap: 40px;
  background: linear-gradient(
    270deg,
    rgba(200, 200, 200, 0.54) 14.79%,
    rgba(204, 204, 204, 0.74) 23.58%,
    rgba(199, 199, 199, 0.01) 61.64%,
    rgba(172, 172, 172, 0.65) 85.07%
  );
  border-bottom: 4px solid rgb(218, 217, 217);
  transition: 0.3s;
}

.header__logo {
  margin-right: auto;
}

.header__menu {
  display: flex;
  gap: 20px;
  align-items: center;
  font-style: normal;
  font-weight: 300;
  font-size: 18px;
  line-height: 30px;
  color: #1e1e1e;
}

.menu__item {
  border-bottom: 2px solid rgba(200, 200, 200, 0.54) 14.79%;
}

.menu__item:hover {
  border-bottom: 2px solid rgb(101, 101, 101);
}

.menu__item:active {
  border-bottom: 2px solid rgb(134, 134, 134);
}

.header__social-net {
  display: flex;
  justify-content: center;
  align-items: center;
}
.header__social-net img {
  /* width: 80px; */
  height: 50px;
}

.header__menu-burger {
  display: none;
}

.menu__logo-block {
  display: flex;
  align-items: center;
  gap: 20px;
}

/* скрытие чекбокса */
#menu__toggle {
  opacity: 0;
}

/* стилизация кнопки */
.menu-btn {
  display: flex;
  align-items: center;
  position: fixed;
  top: 20px;
  left: 20px;

  width: 24px;
  height: 24px;

  cursor: pointer;
  z-index: 1;
}

/* сам гамбургер */
.menu-btn > span,
.menu-btn > span::before,
.menu-btn > span::after {
  display: block;
  position: absolute;

  width: 100%;
  height: 1.5px;
  background-color: #195b98;
}

.menu-btn > span::before {
  content: "";
  top: -8px;
}
.menu-btn > span::after {
  content: "";
  top: 8px;
}


/* Псевдокласс :checked — находит только выбранные или включенные элементы. В нашем случае при активации чекбокса он становится :сhecked. */
#menu__toggle:checked ~ .menu-btn {
  left: 320px !important;
}

#menu__toggle:checked ~ .menu-btn > span {
  transform: rotate(45deg);
  background-color: #195b98;
}
#menu__toggle:checked ~ .menu-btn > span::before {
  top: 0;
  transform: rotate(0);
  background-color: #195b98;
}
#menu__toggle:checked ~ .menu-btn > span::after {
  top: 0;
  transform: rotate(90deg);
  background-color: #195b98;
}
#menu__toggle:checked ~ .menu-box {
  visibility: visible;
  left: 0;
}
/* Комбинатор ~ — находит элементы, указанные справа, которые следуют за элементом, указанным слева и имеют с ним общего родителя. */

/* анимация развертки */
.menu-btn > span,
.menu-btn > span::before,
.menu-btn > span::after,
.menu-box,
.menu-item {
  transition-duration: 0.55s;
}


.menu-box {
  display: block;
  position: fixed;
  visibility: hidden;
  top: 0;
  left: -100%;
  width: 375px;
  height: 100%;
  margin: 0;
  padding: 47px 16px 0 15px;
  color: #1e1e1e;
  background-color: #daedff;
}
.menu-phone {
  margin: 50px 0 40px 0;
  display: flex;
  flex-direction: column;
  gap: 15px;
  font-size: 20px;

}


@media screen and (max-width: 1024px) {
  .header {
    justify-content: flex-end;
    gap: 5px;
    padding-right: 10px;
    padding-left: 60px;
  }
  .header__menu-burger {
    display: block;

  }
  .header__menu {
    display: none;
  }
}
@media screen and (max-width: 568px) {
.header__logo {
  display: none;
}
}