.news-grid {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 40px;
}
.news-item {
    background-color: rgb(238, 238, 238);
    display: flex;
    flex-direction: column;
    gap: 10px;
    width: 300px;
    padding: 20px 15px;
}
.news-item-title {
    margin-top: 10px;
}
.news-item img {
    max-width: 250px;
    max-height: 250px;
}
.button-more {
    border: 1px solid rgb(176, 204, 201);
    width: 120px;
    height: 40px;
    margin-top: 25px;
    font-weight: 600;
    background-color: rgba(75, 75, 255, 0.496);
    border-radius: 4px;
}

.page {
    width: 40px;
    height:40px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 18px;
    font-weight: 500;
    border: 1px solid rgb(50, 50, 50);
}
.news-navigation {
    margin-top: 40px;
    display: flex;
    gap: 10px;
}
.page__current {
    border: 2px solid rgba(75, 75, 255, 0.496);
    font-weight: bold;
}
