.footer {
  margin-top: 50px;
  min-height: 150px;
  padding: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-top: 4px solid rgb(218, 217, 217);
  background: linear-gradient(270deg, rgba(200, 200, 200, 0.54) 14.79%, rgba(204, 204, 204, 0.74) 23.58%, rgba(199, 199, 199, 0.01) 61.64%, rgba(172, 172, 172, 0.65) 85.07%);
}

.footer__partner {
  display: flex;
  gap: 25px;
}

.klio {
 max-width: 100px;
  /* height: 179px; */
}

.presid {
  max-width: 200px;
  /* width: 311px; */
  /* height: 165px; */
}

.nhk {
  max-width: 140px;
  /* width: 168px; */
  /* height: 129px; */
}

.footer__menu {
  display: none;
  flex-direction: column;
  align-items: flex-start;
  gap: 10px;
  font-size: 14px;
  line-height: 30px;
  margin-left: 46px;
  color: #1e1e1e;
}

.footer__contact {
  color: #1e1e1e;
  /* flex: 1 0 400px; */
  display: flex;
  font-size: 14px;
  flex-direction: column;
  justify-content: center;
  gap: 20px;
  max-width: 300px;
}

@media (max-width:915px) {
  .footer__partner {
flex-direction: column;
gap: 20px;
align-items: center;
  }
}

@media (max-width:375px) {
  .footer {
    align-items: center;
    flex-direction: column;
    gap: 40px;
  }
}