.main-articles {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  margin: 0 auto;
  gap: 150px;
}
.main-articles p{
  margin-top: 20px;
  text-align: justify;
  font-size: 20px;
  font-weight: 400;
}
.main-articles section {
  display: flex;
  gap: 30px;
  align-items: center;
  flex-direction: column;
  max-width: 700px;
}
.article-title {
  font-size: 36px;
  font-weight: bold;
}

.article__image-block {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}

.main-articles img {
  min-width: 550px;
    height: auto;
 max-width: 80%;
}
.article-image__title {
  max-width: 80%;
  font-size: 16px;
  font-weight: 400;
}


@media (max-width:915px) {
  .main-articles img {
    min-width: 300px;
    max-width: 70%;
  }
}