.wrapper {
  /* background: #dfdfdf; */

  width: 100%;
  color: #1e1e1e;
}

.content {
  box-sizing:content-box;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.container {
  padding: 64px 128px;
}
p{
  line-height: 140%;
}

iframe {
  border-radius: 4px;
}
.no-active {
  display: none !important;
}
@media screen and (max-width:1024px) {
  .container {
    padding: 64px 20px;
  }
}