.main {
  display: flex;
  flex-direction: column;
  gap: 50px;
  font-size: 20px;
}

.main__about-project {
  max-width: 600px;
}

.main__video-OTV {
  display: flex;
  justify-content: space-between;
  gap: 40px;
  font-size: 18px;
}
.video-OTV__text {
min-width: 200px;
max-width: 600px;
}

.main__photo img {
  max-width: 370px;
}
.main__photo {
  display: grid;
  column-gap: 20px;
  row-gap: 20px;
  grid-template-areas: "aaa b c" "e b f";
}
.photo_2 {
  grid-area: b;
  height: 100%;
}

@media screen and (max-width:1024px) {
  .main__video-OTV {
    flex-direction: column;
    align-items: center;
  }
  .main__photo {
display: flex;
flex-direction: column;
align-items: center;
  }
  .main__photo img {
    max-width: 100%;
  }
}

@media screen and (max-width:534px) {
  .video-OTV__video iframe {
    width: 100%;
    height: auto;
  }
  
}