.about {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 120px;
}
.about h1 {
    font-weight: bold;
    font-size: 48px;

}
.about h2 {
    font-weight: bold;
    font-size: 28px;
}
.about p {
    font-size: 20px;
}
.about__block {
    display: flex;
    justify-content: space-between;
    gap: 20px;
}
.about-block__left {
    display: flex;
    flex-direction: column;
    gap: 50px;
    max-width: 500px;
    align-items: center;
}
.about-block__left img{
    object-fit: cover;
    width: 100%;
max-width: 500px;

}
.about-block__right {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    max-width: 500px;
    gap: 50px;
}
.about-block__right img{
    max-width: 760px;
    max-height: 420px;
}

.about__text {
    align-self: center;
    max-width: 500px;
    font-weight: 500;
}
.about__talk-teen, .about__mediacenter {
    max-width: 80%;
    display: flex;
    flex-direction: column;
    gap: 20px;
    align-items: flex-start;
}


.talk-teen__text {
    display: flex;
    gap: 20px;
}

@media (max-width: 1024px) {
    .about {
        align-items: flex-start;
    }
    .about h2 {
        font-size: 24px;
        line-height: 30px;
    }
    .about img {
        max-width: 90% !important;
    }
    .about__block {
        flex-direction: column;
        align-items: center;
        justify-content: flex-start;
    }
    .about-block__left {
        align-items: flex-start;
    }
    .talk-teen__text {
        flex-wrap: wrap;
    }
}