.ancient-prim {
  line-height: 30px;
  display: flex;
  flex-direction: column;
  padding: 64px 0;
}

.ancient-prim__about-prim {
  display: flex;
  flex-direction: column;
  gap: 40px;
}

.about-prim {
  display: flex;
  flex-direction: column;
  row-gap: 20px;
  max-width: 500px;
}

.title {
  font-size: 48px;
  font-weight: bold;
  text-align: left;
  line-height: 130%;
}

.text {
  font-size: 24px;
}

hr {
  margin: 55px 0px;
  border-top: 2px solid black;
}

.ancient-prim__about-project {
  max-width: 500px;
  align-self: flex-end;
}

.ancient-prim__stone-cent {
  display: flex;
  flex-direction: column;
  gap: 40px;
}

.timeline__top {
  display: flex;
  justify-content: space-between;
  padding: 0 7.9%;
}

.timeline__image {
  max-width: 100%;
}

.item__title {
  font-size: 32px;
  font-weight: bold;
}

.item__subtitle {
  font-size: 20px;
}

.top__text {
  width: 130px;
  text-align: center;
  margin-right: -7px;
}

.timeline__text {
  display: flex;
  justify-content: space-between;
  padding: 0 7.9%;
}

.item__text {
  width: 130px;
  text-align: center;
  margin-right: -7px;
  font-size: 24px;
  font-weight: bold;
}

.stone-cent__paleolit {
  display: flex;
  justify-content: space-between;
  margin-top: 40px;
  gap: 40px;
}
.paleolit__head,
.neolit__head {
  display: flex;
  flex-direction: column;
  gap: 20px;

  align-self: center;
  text-align: center;
}

.head__text-section {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.head__title {
  font-size: 48px;
  font-weight: bold;
}

.head__subtitle {
  font-size: 32px;
}

.paleolit__first-people {
  max-width: 500px;
  display: flex;
  flex-direction: column;
  gap: 40px;
}

.first-people__line {
  margin: 0;
}

.first-people__text {
  font-size: 20px;
}

.first-people__first-words {
  font-size: 24px;
  font-weight: bold;
}

.stone-cent__about {
  display: flex;
  justify-content: space-between;
  gap: 20px;
  font-size: 20px;
}

.about__left {
  max-width: 600px;
}

.about__left hr {
  margin: 39px 0;
}

.left__text-block {
  display: flex;
  flex-direction: column;
  gap: 40px;
}

.text-block__image {
  max-width: 600px;
  max-height: 400px;
}

.about__right {
  max-width: 600px;
}

.text-block__arrow {
  max-width: 100px;
}

.stone-cent__finding {
  font-size: 24px;
  max-width: 800px;
  margin: 0 auto;
}

.finding__first-word {
  font-weight: bold;
  font-size: 30px;
}

.stone-cent__arrow-okladnikov {
  display: flex;
  width: 100%;
  justify-content: center;
  gap: 40px;
  font-size: 24px;
  flex-wrap: wrap;
}

.arrow-okladnikov__column {
  max-width: 500px;
}

.column__text {
  margin-top: 30px;
}

/* Блок с древними животными */

.stone-cent__anciant-animals {
  margin-top: 100px;
  display: flex;
  justify-content: space-between;
  gap: 40px;
}
.anciant-animals__left {
  display: flex;
  flex-direction: column;
  /* width: 60%; */
  justify-content: space-between;
  gap: 100px;
  margin-top: 40px;
}
.left__section-picture {
  display: flex;
  flex-wrap: wrap;
  gap: 40px;
  /* max-height: 800px; */
  justify-content: center;
  align-items: center;
}
.anciant-animals-image {
  width: 500px;
}

.anciant-animals__text {
  font-size: 24px;
}
.anciant-animals__right {
  /* width: 40%; */
  margin-top: 40px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.animals {
  width: 80%;
}
.neolit__head {
  margin-top: 200px;
  margin-bottom: 100px;
}

.stone-cent__neolit {
  font-size: 24px;
  display: flex;
  justify-content: space-between;
  gap: 40px;
}
.stone-cent__neolit hr {
  margin: 0;
}
.neolit__left {
  width: 60%;
  display: flex;
  flex-direction: column;
  gap: 70px;
}
.neolit__left-text b {
  font-size: 30px;
}
.sedentary-lifestyle {
  font-size: 30px;
  font-weight: bold;
}
.neolit__left-wheel {
  display: grid;
  grid-template-areas: "ceramik empty" "empry wheel";
}
@media (max-width: 1024px) {
  .neolit__left-wheel {
    display: flex;
    flex-direction: column;
    gap: 20px;
    align-items: center;
  }
  .neolit__left-wheel img {
    max-width: 100%;
  }
}
.left-wheel {
  width: 100%;
}
.neolit__right {
  display: flex;
  flex-direction: column;
  gap: 70px;
  width: 40%;
}

.neolit__last-text {
  font-size: 28px;
  margin-top: 30px;
  font-weight: bold;
  align-self: center;
  max-width: 700px;
}
@media (max-width: 1024px) {
  .neolit__left, .neolit__right {
    width: 100%;
  }
  .ancient-prim img {
    min-width: 300px !important;
    width: 90% !important;
  }
  .ancient-prim p,
  .ancient-prim span {
    font-size: 18px;
  }
  .stone-cent__about {
    flex-direction: column;
    align-items: center;
  }
  .anciant-animals__left, .anciant-animals__right {
    width: 100%;
  }
  .left__section-picture {
    flex-direction: column;
  }
  .left__text-block {
    align-items: center;
  }
  .stone-cent__neolit {
    flex-direction: column;
    align-items: center;
  }
  .stone-cent__paleolit {
    flex-wrap: wrap;
    align-items: center;
  }
  .stone-cent__anciant-animals {
    flex-direction: column-reverse;
    text-align: justify;
    align-items: center;
  }

  .anciant-animals__right {
    width: 100%;
  }
}
